.point {
    padding: 120px 0;
    background: url("../img/bg_ellipse_white.png") center / cover no-repeat;
    background-color: $main-color1;

    &__img {
        max-width: 447px;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__wrapper {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
    }

    &__before {
        margin-top: 103px;
        display: flex;
        flex-direction: column;
        width: 552px;
        text-align: right;
    }

    &__after {
        margin-top: 80px;
        display: flex;
        flex-direction: column;
        width: 545px;
        text-align: left;
    }

    &__number {
        font-weight: 700;
        font-size: 280px;
        line-height: 280px;
        text-transform: capitalize;
        color: $main-color2;
    }

    &__descr {
        margin-top: 28px;
        font-weight: 700;
        font-size: 32px;
        line-height: 119%;
        color: $main-color2;
    }
}

@media (max-width: 1679px) {
    .container {
        max-width: 1320px;
    }

    .point {
        padding: 100px 0;

        /* min-height: 725px; */

        &__before {
            text-align: center;
        }

        &__after {
            text-align: center;
        }

        &__number {
            font-size: 170px;
            line-height: 170px;
        }

        &__descr {
            font-size: 31px;
        }
    }
}

@media (max-width: 1440px) {
    .container {
        max-width: 1120px;
    }

    .point {
        /* min-height: 690px; */
        &__descr {
            font-size: 30px;
        }
    }
}

@media (max-width: 1200px) {
    .container {
        max-width: 968px;
    }

    .point {
        padding: 100px 0;

        /* min-height: 600px; */

        &__number {
            font-size: 130px;
            line-height: 130px;
        }

        &__descr {
            font-size: 24px;
        }
    }
}

@media (max-width: 992px) {
    .container {
        max-width: 720px;
    }

    .point {
        padding: 80px 0;

        &__number {
            font-size: 125px;
            line-height: 125px;
        }

        &__descr {
            font-size: 24px;
        }
    }
}

@media (max-width: 768px) {
    .container {
        max-width: 100%;
        padding: 0 20px;
    }

    .point {
        position: relative;

        /* min-height: 796px; */
        padding: 80px 0;
        background: url("../img/bg_ellipse_white-mobile.png") center / cover no-repeat;
        background-color: $main-color1;

        &__img {
            position: absolute;
            top: 158px;
            left: 50%;
            min-width: 390px;
            transform: translateX(-50%);
        }

        &__wrapper {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            min-height: 636px;
        }

        &__before {
            z-index: 10;
            max-width: 100%;
            margin-top: 0;
            text-align: left;
        }

        &__after {
            z-index: 10;
            width: 100%;
            margin-top: 0;
            text-align: right;
        }

        &__number {
            font-size: 128px;
            line-height: 90px;
        }

        &__descr {
            margin-top: 40px;
            font-size: 22px;
            line-height: 25px;
        }
    }
}

@media (max-width: 429px) {
    .container {
        max-width: 100%;
        padding: 0 20px;
    }

    .point {
        &__number {
            font-size: 110px;
        }

        &__descr {
            font-size: 20px;
        }

        &__img {
            min-width: 320px;
        }
    }
}